import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { PropTypes } from "prop-types";
import { connect } from "react-redux";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";
class NavBar extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar color="light" light expand="md">
          <NavbarBrand href="/">Hi! I'm Michael</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink href="#eduTitle" className="mx-2">
                  EDUCATION
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#expTitle" className="mx-2">
                  EXPERIENCE
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#skiTitle" className="mx-2">
                  SKILLS
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#actTitle" className="mx-2">
                  ACTIVITIES
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/components/" className="mx-2">
                  CONTACT
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}

export default NavBar;
